import { Injectable, Inject, Injector, EventEmitter, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import * as _ from 'lodash';

import { of, Subject, Subscription, fromEvent, timer, Observable } from 'rxjs';
import { tap, map, switchMap, retryWhen, delay, debounceTime, startWith } from 'rxjs/operators';

import { StorageVars } from './models/storage-vars';

import { LoginRequest } from './models/login-request';
import { LoginResponse } from './models/login-response';
import { LogoffRequest } from './models/logoff-request';
import { LogoffResponse } from './models/logoff-response';
import { IsValidTokenRequest } from './models/is-valid-token-request';
import { OperationResult } from './models/operation-result';
import { TbAuthEnvironment } from './models/auth-environment';
import { AuthConnectionOptions } from './models/auth-connection-options';
import { AuthConnectionState } from './models/auth-connection-state';

@Injectable({
    providedIn: 'root'
})
export class TbAuthService implements OnDestroy {
    private static DEFAULT_ENV: TbAuthEnvironment = {
        auth: {
            url: 'asdf',
            subscriptionSelection: false,
            appId: 'M4',
            redirectUrl: '/',
            sessionStorage: false,
            logo:
                // tslint:disable-next-line: max-line-length
                'data:image/png;base64,'
        }
    };
    private env: TbAuthEnvironment;

    private stateChangeEventEmitter = new EventEmitter<AuthConnectionState>();
    private currentConnectionState: AuthConnectionState = {
        hasBackendAccess: false,
        hasNetworkConnection: window.navigator.onLine
    };
    private offlineSubscription!: Subscription;
    private onlineSubscription!: Subscription;
    private httpSubscription!: Subscription;
    private serviceOptions: AuthConnectionOptions;

    loggedOut$ = new Subject();
    errorMessage = '';

    get router() {
        return this.injector.get<Router>(Router);
    }

    constructor(@Inject('env') env: TbAuthEnvironment, private http: HttpClient, private injector: Injector) {
        this.env = _.defaults({}, env, TbAuthService.DEFAULT_ENV);
        console.log('this.env', this.env);
        this.serviceOptions = new AuthConnectionOptions(this.getBaseUrl());

        this.checkNetworkState();
        this.checkBackendState();
    }

    private checkBackendState() {
        if (this.httpSubscription) {
            this.httpSubscription.unsubscribe();
        }

        if (this.serviceOptions.enableHeartbeat) {
            this.httpSubscription = timer(0, this.serviceOptions.heartbeatInterval)
                .pipe(
                    switchMap(() => this.http.get(this.serviceOptions.heartbeatUrl, { responseType: 'text' })),
                    retryWhen(errors =>
                        errors.pipe(
                            // log error message
                            tap(val => {
                                console.error('Http error:', val);
                                this.currentConnectionState.hasBackendAccess = false;
                                this.emitEvent();
                            }),
                            // restart after 5 seconds
                            delay(this.serviceOptions.heartbeatRetryInterval)
                        )
                    )
                )
                .subscribe(result => {
                    this.currentConnectionState.hasBackendAccess = true;
                    this.emitEvent();
                });
        } else {
            this.currentConnectionState.hasBackendAccess = false;
            this.emitEvent();
        }
    }

    private checkNetworkState() {
        this.onlineSubscription = fromEvent(window, 'online').subscribe(() => {
            this.currentConnectionState.hasNetworkConnection = true;
            this.checkBackendState();
            this.emitEvent();
        });

        this.offlineSubscription = fromEvent(window, 'offline').subscribe(() => {
            this.currentConnectionState.hasNetworkConnection = false;
            this.checkBackendState();
            this.emitEvent();
        });
    }

    private emitEvent() {
        this.stateChangeEventEmitter.emit(this.currentConnectionState);
    }

    /**
     * Monitor Network & Internet connection status by subscribing to this observer.
     * If you set "reportcurrentConnectionState" to "false" then
     * function will not report current status of the connections when initially subscribed.
     * @param reportcurrentConnectionState Report current state when initial subscription. Default is "true"
     */
    monitorConnectionStatus(reportcurrentConnectionState = true): Observable<AuthConnectionState> {
        return reportcurrentConnectionState
            ? this.stateChangeEventEmitter.pipe(
                  debounceTime(300),
                  startWith(this.currentConnectionState)
              )
            : this.stateChangeEventEmitter.pipe(debounceTime(300));
    }

    /**
     * Ritorna la base url del backend,
     * caricata da un file di configurazione caricato dinamicamente (assets/environment.json)
     */
    getBaseUrl() {
        return this.env.auth.url;
    }

    /*
      {
        type: JWT,
        appid: M4,
        securityValue: jwtEncoded
      }
    */
    getAuthorizationHeader(): string {
        return JSON.stringify({
            Type: 'JWT',
            AppId: 'M4',
            SecurityValue: this.getToken()
        });
    }

    login(loginRequest: LoginRequest): Promise<LoginResponse> {
        // console.log('authService.login - loginRequest', loginRequest);
        return this.http
            .post<LoginResponse>(this.getLoginUrl(), loginRequest)
            .pipe(
                map((loginResponse: LoginResponse) => {
                    if (!loginResponse.Result) {
                        this.clearStorage();
                        loginResponse.Message = loginResponse.Message ? loginResponse.Message : 'Login error...';
                        this.errorMessage = loginResponse.Message;
                        return loginResponse;
                    }

                    this.storageData(loginResponse);

                    return loginResponse;
                })
            )
            .toPromise();
    }

    async isValidToken(authtoken = '') {
        if (!authtoken) {
            return of(false);
        }

        return this.http
            .post<OperationResult>(this.getIsValidTokenUrl(), new IsValidTokenRequest(authtoken))
            .pipe(
                tap((jObj: any) => {
                    // console.log('isValidToken - response', jObj);
                    if (!jObj.Result) {
                        jObj.Message = jObj.Message ? jObj.Message : 'isValidToken error...';
                        this.clearStorage();
                        this.errorMessage = jObj.Message;
                    }
                })
            )
            .toPromise();
    }

    public getCompaniesForUser(user: string) {
        return this.http.get<OperationResult>(this.getSubsKeysForAccountUrl() + user).pipe(
            map((res: any) => {
                if (!res || !res.Result) return [];
                return res.Content && res.Content.subscriptions ? res.Content.subscriptions : [];
            })
        );
    }

    public getIsValidTokenUrl() {
        return this.getBaseUrl() + 'isvalidtoken/';
    }

    public getLoginUrl() {
        return this.getBaseUrl() + 'login/';
    }

    public getLogoutUrl() {
        return this.getBaseUrl() + 'logoff/';
    }

    public getChangePasswordUrl() {
        return this.getBaseUrl() + 'changepassword/';
    }

    public getResetPasswordUrl() {
        return this.getBaseUrl() + 'resetpassword/';
    }

    public getSubsKeysForAccountUrl() {
        return this.getBaseUrl() + 'subscriptionskeysforaccount/';
    }

    public logoff() {
        const logoffRequest: LogoffRequest = new LogoffRequest(this.getToken());

        return this.http
            .post<LogoffResponse>(this.getLogoutUrl(), logoffRequest)
            .pipe(
                map((logoffResponse: LogoffResponse) => {
                    if (logoffResponse.Result) {
                        this.clearStorage();
                        this.loggedOut$.next();
                    }

                    return logoffResponse;
                })
            )
            .toPromise();
    }

    public clearStorage() {
        sessionStorage.removeItem(StorageVars.JWT);
        sessionStorage.removeItem(StorageVars.CULTURE);
        sessionStorage.removeItem(StorageVars.UI_CULTURE);
        sessionStorage.removeItem(StorageVars.ACCOUNT_ROLES);

        localStorage.removeItem(StorageVars.JWT);
        localStorage.removeItem(StorageVars.CULTURE);
        localStorage.removeItem(StorageVars.UI_CULTURE);
        localStorage.removeItem(StorageVars.ACCOUNT_ROLES);
    }

    storageSubscriptionData(subscriptionKey: string, subscriptionDescription: string) {
        if (this.env.auth.sessionStorage) {
            sessionStorage.setItem(StorageVars.SUBSCRIPTION, subscriptionKey);
            sessionStorage.setItem(StorageVars.SUBSCRIPTION_DESCRIPTION, subscriptionDescription);
        } else {
            localStorage.setItem(StorageVars.SUBSCRIPTION, subscriptionKey);
            localStorage.setItem(StorageVars.SUBSCRIPTION_DESCRIPTION, subscriptionDescription);
        }
    }

    private storageData(loginResponse: LoginResponse) {
        const respCulture: string =
            loginResponse.RegionalSettings === undefined || loginResponse.RegionalSettings.length === 0
                ? window.navigator.language
                : loginResponse.RegionalSettings;
        const respUiCulture: string =
            loginResponse.Language === undefined || loginResponse.Language.length === 0
                ? window.navigator.language
                : loginResponse.Language;

        if (this.env.auth.sessionStorage) {
            sessionStorage.setItem(StorageVars.JWT, loginResponse.JwtToken);
            sessionStorage.setItem(StorageVars.CULTURE, respCulture);
            sessionStorage.setItem(StorageVars.UI_CULTURE, respUiCulture);
            sessionStorage.setItem(StorageVars.ACCOUNT_ROLES, JSON.stringify(loginResponse.Roles));
        } else {
            localStorage.setItem(StorageVars.JWT, loginResponse.JwtToken);
            localStorage.setItem(StorageVars.CULTURE, respCulture);
            localStorage.setItem(StorageVars.UI_CULTURE, respUiCulture);
            localStorage.setItem(StorageVars.ACCOUNT_ROLES, JSON.stringify(loginResponse.Roles));
        }
    }

    saveCulture(culture: string, uiCulture: string) {
        if (this.env.auth.sessionStorage) {
            sessionStorage.setItem(StorageVars.CULTURE, culture);
            sessionStorage.setItem(StorageVars.UI_CULTURE, uiCulture);
        } else {
            localStorage.setItem(StorageVars.CULTURE, culture);
            localStorage.setItem(StorageVars.UI_CULTURE, uiCulture);
        }
    }

    getToken() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.JWT);
        else return localStorage.getItem(StorageVars.JWT);
    }

    getAccountName() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.ACCOUNT_NAME);
        else return localStorage.getItem(StorageVars.ACCOUNT_NAME);
    }

    getSubscription() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.SUBSCRIPTION);
        else return localStorage.getItem(StorageVars.SUBSCRIPTION);
    }

    getSubscriptionDescription() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.SUBSCRIPTION_DESCRIPTION);
        else return localStorage.getItem(StorageVars.SUBSCRIPTION_DESCRIPTION);
    }

    getCulture() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.CULTURE);
        else return localStorage.getItem(StorageVars.CULTURE);
    }

    getUICulture() {
        if (this.env.auth.sessionStorage) return sessionStorage.getItem(StorageVars.UI_CULTURE);
        else return localStorage.getItem(StorageVars.UI_CULTURE);
    }

    getAuthServiceUrl = (): string => this.env.auth.url;
    getRedirectUrl = (): string => this.env.auth.redirectUrl;
    hasSubscriptionSelection = (): boolean => this.env.auth.subscriptionSelection;
    getAppId = (): string => this.env.auth.appId;
    isSessionStorage = (): boolean => this.env.auth.sessionStorage;
    getCustomLogo = (): string => this.env.auth.logo;

    stopCheckingConnection() {
        try {
            this.offlineSubscription.unsubscribe();
            this.onlineSubscription.unsubscribe();
            this.httpSubscription.unsubscribe();
        } catch (e) {
            console.error('TbAuthService.ngOnDestroy()');
        }
    }

    ngOnDestroy(): void {
        this.stopCheckingConnection();
    }
}
